@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,500,700,900");

body {
    font-family: "Roboto", "Helvetica Neue", Helvetica, Arial;
    font-size: 0.8125rem;
    letter-spacing: 0.1px;
}

.page-content {
    color: #666666;
}

h1, h2, h3, h4, h5, h6 {
    line-height: 1.3;
    font-weight: 400;
}

strong {
    font-weight: 500;
}

h1 small,
h2 small,
h3 small,
h4 small,
h5 small,
h6 small,
.h1 small,
.h2 small,
.h3 small,
.h4 small,
.h5 small,
.h6 small {
    font-weight: 300;
    display: block;
    font-size: 0.9375rem;
    line-height: 1.5;
    margin: 2px 0 1.5rem;
}

h2 small,
h3 small,
.h2 small,
.h3 small {
    font-size: 0.9375rem;
}

h4 small,
.h4 small {
    font-size: 0.875rem;
}

h5 small,
h6 small,
.h5 small,
.h6 small {
    font-size: 0.8125rem;
}

/* contrast text */
.text-contrast {
    color: #333333;
}

/* text-gradient */
.text-gradient {
    background: -webkit-gradient(linear, left top, left bottom, color-stop(25%, #6e4e9e), color-stop(50%, #62468d), color-stop(75%, #0c7cd5), to(#0960a5));
    background: linear-gradient(180deg, #6e4e9e 25%, #62468d 50%, #0c7cd5 75%, #0960a5 100%);
    color: #886ab5;
    background-clip: text;
    text-fill-color: transparent;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: none;
}

